@import "./_job.less";
@job-img-dir: 'job/_images';


.@{container-class}{

	.resume{
		width:100%;
		margin-top:5%;
		padding:0 2% 0 2%;
		overflow:hidden;
		background:#fff;

		.content{
			color:#434343;
			overflow:hidden;

			li{
				border-top:1*@rex solid #D5D5D5;
				line-height:50*@rex;
				width:100%;
				float:left;
				position:relative;
			}

			input[type=text]{
				height:22*@rex;
				.margin(13*@rex,_,13*@rex,_);
				float:left;
			}

			span{
				display:block;
				width:80*@rex;
				float:left;
				margin-right:5*@rex;
			}

			.on{
				width:110*@rex;
			}

			.down-menu{
				overflow:hidden;
				position: relative;
				width:22%;
				height:50*@rex;
				float:left;
				margin-right:5*@rex;

				.down{
					width:100%;
					height:20*@rex;
					line-height:20*@rex;
					border:1*@rex solid #4C4C4C;
					text-indent:2*@rex;
					border-radius: 4*@rex;
					background: -webkit-linear-gradient(#FEFEFE ,#D3D3D3);
					background: -o-linear-gradient(#FEFEFE ,#D3D3D3);
					background: -moz-linear-gradient(#FEFEFE ,#D3D3D3);
					background: linear-gradient(#FEFEFE ,#D3D3D3);
					margin:15*@rex 0 0 0;
					float:left;
					position: relative;
					display:block;
					z-index:10;
					background:transparent;
					appearance: none;
				}

				&:after{
					content:" ";
					width:21*@rex;
					height:20*@rex;
					display: block;
					.absolute(15*@rex,0,_,_);
					z-index:0;
					background: data-uri('@{job-img-dir}/down.png');
					background-repeat:no-repeat;
					background-size: 100% 100%;
					display: block;
				}
			}

			.gen{
				margin-left:80*@rex;

				.label{
					display: inline-block;
					margin-right:10*@rex;
					position: relative;
					width:25%;


					input{
						min-width:0;
						opacity: 0;
						width:22*@rex+1*@rex;
						height:22*@rex+1*@rex;
						text-align:center;
						outline:0;
						.absolute(0,_,_,0);
						float: left;
						margin-top:13*@rex;

					}

					input:checked + .copy-box {
						background:data-uri('@{job-img-dir}/input.png');
						background-size:100% 100%;
					}

					.copy-box{
						display:block;
						float: left;
						border:1*@rex solid #ADADAD;
						border-radius: 22*@rex;
						.size(22*@rex);
						cursor:pointer;
						.margin(13*@rex,5*@rex,_,_);
					}

				}

			}

			.frame{
				border:1*@rex solid #ADADAD;
				height:90*@rex;
				resize:none;
				width:94%;
				.margin(_,3%,_,3%);
			}
		}

		a.submit{
			width:94%;
			.margin(_,3%,6%,3%);
			background:#C20053;
			border-radius: 4*@rex;
			line-height:50*@rex;
			text-align:center;
			color:#fff;
			border:none;
			display:block;
			float:left;
		}

	}
}